import React from 'react';
import PropTypes from 'prop-types';
import PrimaryGap from '../BaseComponents/PrimaryGap';
import { SettingsContainer, SubTitle, LinkItem } from './index';
import Head from '../container/settingPages/Head';

function genLinkItem({
  key, title, to, subTitle,
}) {
  return <LinkItem key={key} to={to} title={title} subTitle={subTitle} />;
}
genLinkItem.propTypes = {
  key: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  to: PropTypes.string.isRequired,
  subTitle: PropTypes.string,
};
genLinkItem.defaultProps = {
  subTitle: '',
};

export default function Account({ user, language, country }) {
  return (
    <SettingsContainer>
      <Head title="" />
      <SubTitle></SubTitle>
      {
        [
          {
            key: 1, to: '/settings/screen_name', title: '', subTitle: user.name,
          },
          {
            key: 2, to: '/settings/phone', title: '', subTitle: String(user.phone),
          },
          {
            key: 3, to: '/settings/email', title: '', subTitle: user.email,
          },
          {
            key: 4, to: '/settings/password', title: '',
          },
          {
            key: 5, to: '/settings/security', title: '',
          },
        ].map(genLinkItem)
      }
      <SubTitle></SubTitle>
      {
        [
          {
            key: 1, to: '/settings/language', title: '', subTitle: language,
          },
          {
            key: 2, to: '/settings/country', title: '/', subTitle: country,
          },
          {
            key: 3, to: '/settings/your_data', title: '',
          },
          {
            key: 4, to: '/settings/applications', title: '',
          },
        ].map(genLinkItem)
      }
      <PrimaryGap />
      <LinkItem to="/settings/deactivate" title="" />
    </SettingsContainer>
  );
}
Account.propTypes = {
  user: PropTypes.shape({
    name: PropTypes.string.isRequired,
    phone: PropTypes.number.isRequired,
    email: PropTypes.string.isRequired,
  }).isRequired,
  language: PropTypes.string.isRequired,
  country: PropTypes.string.isRequired,
};
