import React, { useState } from 'react';
import PropTypes from 'prop-types';
import ReactRouterPropTypes from 'react-router-prop-types';
import PrimaryGap from '../BaseComponents/PrimaryGap';
import { SettingsContainer, CustomizedInput } from './index';
import Head from '../container/settingPages/Head';
import CustomizedButton from '../BaseComponents/CustomizedButton';
import Text from '../BaseComponents/Text';

function Warning({ validate }) {
  return (
    <div>
      {
        !validate
        && <Text small warning></Text>
      }
    </div>
  );
}
Warning.propTypes = {
  validate: PropTypes.bool.isRequired,
};

function isPhone(value) {
  const reg = /^[+86]?[\b]*[0-9]{11}$/;
  return reg.test(value);
}

export default function Phone({ phone, setPhone, history }) {
  const [newPhone, setNewPhone] = useState(0);
  function handleChange(e) {
    setNewPhone(e.target.value);
  }
  function handleSave() {
    setPhone(newPhone);
    history.goBack();
  }
  return (
    <SettingsContainer>
      <Head title="" />
      <CustomizedInput
        labelText=""
        value={phone}
        readOnly
        onChange={null}
      />
      <CustomizedInput
        labelText=""
        value={newPhone}
        onChange={handleChange}
        autoFocus
        WarningLabel={() => <Warning validate={isPhone(newPhone)} />}
      />
      <PrimaryGap />
      <div style={{ padding: '9px', backgroundColor: 'white' }}>
        <Text secondary>
        
        
        </Text>
      </div>
      <PrimaryGap />
      <div style={{ padding: '14px 9px', backgroundColor: 'white', textAlign: 'center' }}>
        <Text warning>
          
        </Text>
      </div>
      <PrimaryGap />
      <div style={{
        display: 'flex', justifyContent: 'flex-end', padding: '9px', backgroundColor: 'white',
      }}
      >
        <CustomizedButton
          filled
          onClick={handleSave}
          disabled={
            newPhone === phone
            || !isPhone(newPhone)
          }
        >

        </CustomizedButton>
      </div>
    </SettingsContainer>
  );
}
Phone.propTypes = {
  phone: PropTypes.number.isRequired,
  setPhone: PropTypes.func.isRequired,
  history: ReactRouterPropTypes.history.isRequired,
};
