import React from 'react';
import PropTypes from 'prop-types';
import {
  SettingsContainer, SubTitle, LinkItem, CheckBox,
} from './index';
import Head from '../container/settingPages/Head';

const personalizationLabels = { 0: '', 1: '', 2: '' };

function generateLinkItem({
  key, to, title, subTitle,
}) {
  return <LinkItem key={key} to={to} title={title} subTitle={subTitle} />;
}
generateLinkItem.propTypes = {
  key: PropTypes.number.isRequired,
  to: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  subTitle: PropTypes.string,
};
generateLinkItem.defaultProps = {
  subTitle: '',
};

export default function Safety({ user, personalization }) {
  return (
    <SettingsContainer>
      <Head title="" />
      <SubTitle></SubTitle>
      <CheckBox title="" subTitle="" />
      {
        [
          {
            key: 1, to: '/settings/location', title: '', subTitle: user.name,
          },
          {
            key: 2, to: '/settings/tagging', title: '', subTitle: '',
          },
        ].map(generateLinkItem)
      }

      <SubTitle></SubTitle>
      <CheckBox title="" subTitle="" />
      <CheckBox title="" subTitle="" />

      <SubTitle></SubTitle>
      <LinkItem to="/settings/contacts" title="" />

      <SubTitle></SubTitle>
      <CheckBox title="" subTitle="" />
      <CheckBox title="" subTitle="" />
      {
        [
          {
            key: 1, to: '/settings/mute', title: '',
          },
          {
            key: 2, to: '/settings/blocked/all', title: '',
          },
          {
            key: 3, to: '/settings/notifications', title: '',
          },
          {
            key: 4, to: '/settings/search', title: '',
          },
        ].map(generateLinkItem)
      }

      <SubTitle></SubTitle>
      <LinkItem to="/settings/personalization" title="" subTitle={personalizationLabels[personalization]} />

      <SubTitle>Twitter</SubTitle>
      <LinkItem to="/settings/teams" title="Twitter" />
    </SettingsContainer>
  );
}
Safety.propTypes = {
  user: PropTypes.shape({
    name: PropTypes.string.isRequired,
  }).isRequired,
  personalization: PropTypes.oneOf([0, 1, 2]).isRequired,
};
