import React, { useState } from 'react';
import PropTypes from 'prop-types';
import ReactRouterPropTypes from 'react-router-prop-types';
import PrimaryGap from '../BaseComponents/PrimaryGap';
import { SettingsContainer, CustomizedInput } from './index';
import Head from '../container/settingPages/Head';
import CustomizedButton from '../BaseComponents/CustomizedButton';
import Text from '../BaseComponents/Text';

function isEmail(value) {
  const reg = /^[a-zA-Z.]+@[a-zA-Z]+.[a-zA-Z]+$/;
  return reg.test(value);
}

export default function Email({ email, setEmail, history }) {
  const [newEmail, setNewEmail] = useState('');
  function handleChange(e) {
    setNewEmail(e.target.value);
  }
  function handleSave() {
    setEmail(newEmail);
    history.goBack();
  }
  return (
    <SettingsContainer>
      <Head title="" />
      <PrimaryGap />
      <CustomizedInput
        labelText=""
        readOnly
        value={email}
        onChange={null}
      />
      <CustomizedInput
        labelText=""
        value={newEmail}
        onChange={handleChange}
        autoFocus
      />
      <PrimaryGap />
      <div style={{ padding: '9px', backgroundColor: 'white' }}>
        <Text secondary>
         Twitter 
        </Text>
      </div>
      <PrimaryGap />
      <div style={{
        display: 'flex', justifyContent: 'flex-end', padding: '9px', backgroundColor: 'white',
      }}
      >
        <CustomizedButton
          filled
          onClick={handleSave}
          disabled={
            newEmail === email
            || !isEmail(email)
          }
        >

        </CustomizedButton>
      </div>
    </SettingsContainer>
  );
}
Email.propTypes = {
  email: PropTypes.string.isRequired,
  setEmail: PropTypes.func.isRequired,
  history: ReactRouterPropTypes.history.isRequired,
};
